html {
  height: 100%;
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
  padding-bottom: 6rem;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    color: white;
}

.navbar-nav > a:hover {
    color: pink;
}


a:hover {
    color: white;
}


.pageFooter {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #007bff;
}

.footerText {
    text-align: center;
    color: white;
    padding: 4px;
}

.fab {
    margin-left: 4px;
}


.resultsContainer {
    margin-top: 40px;
}

.titleRow {
    margin-left: 28px;
}

.listItem {
    padding: 28px;
    border: 1px solid black;
    text-align: justify;
}

img {
    margin-bottom: 16px;
}


